<template>
    <div class="section-container">
        <h2>  </h2>
        <div class="slider-wrap">
            <swiper
                :autoplay="{
                    delay: 2500,
                    disableOnInteraction: false,
                    }"
                :navigation="true"
                :slidesPerView="3"
                :spaceBetween="30"
                :pagination="{
                clickable: true,
                }"
                :modules="modules"
                class="mySwiper"
            >
                <swiper-slide>
                    <img src="../assets/new-s-1.jpg" alt="">
                </swiper-slide>
                <swiper-slide>
                    <img src="../assets/new-s-2.jpg" alt="">
                </swiper-slide>
                <swiper-slide>
                    <img src="../assets/new-s-3.jpg" alt="">
                </swiper-slide>
                <swiper-slide>
                    <img src="../assets/new-s-4.jpg" alt="">
                </swiper-slide>
                <swiper-slide>
                    <img src="../assets/new-s-5.jpg" alt="">
                </swiper-slide>
